@import url('https://fonts.googleapis.com/css?family=Lato:400,700|Open+Sans:600');

body {
    margin: 0;
    padding: 0;
    font-family: 'Lato', sans-serif;
    color: #333333;
}

.accueil .container header {
    background: url('/images/building_3.jpg');
    height: 70vh;
}

.accueil .contenu {
    background: #085394;
    padding: 30px 0;
}

.accueil .intro{
    width:80%;
    text-align: center;
    margin: 0 auto;
    max-width:1000px;
    font-size:25px;
    line-height:150%;
    padding-top:130px;
}

.accueil .intro p {
   color: white;
}

.blocks {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    max-width: 1300px;
    width: 90%;
    margin: 0 auto;
}

.blocks .block hr {
    background: white;
    width: 60px;
    height: 0.2px;
    border: none;
}

.blocks .block h3 {
    color: white;
}

.blocks .block p {
    color: #6FA8DC;
}

.blocks .block {
    text-align: center;
    width: 30%;
    max-width: 350px;
}

.se_container {
    display: -ms-flexbox;
    display: flex;
    height: 80vh;
}

.se_container .right_menu {
    margin-left: 50px;
    -ms-flex-item-align: center;
        align-self: center;
}

.se_container .img_container {
    width: 50%;
    height: 100%;
    background: url('/images/pexels-photo-630839.jpeg') center center;
    background-size: cover;

}

.se_container .img_container .text {
    bottom: 50px;
    background: rgba(245, 198, 98, 0.9);
    max-width: 550px;
}

.se_container .img_container p {
    font-family: 'Open Sans', sans-serif;
    font-style: normal;
    font-weight: 600;
    line-height: 46px;
    font-size: 28px;
}

.se_menu .text {
    max-width: 550px;
}

.category_title {
    text-transform: uppercase;
    font-family: "Open Sans", sans-serif;
    font-size: 64px;
    line-height: 78px;
    max-width: 470px;
}

.category_title_container {
    position: relative;
}

.category_title_bg {
    background: #f2b22a;
    height: 156px;
    z-index: -5;
    width: 165px;
    position: absolute;
    left: 0;
}

.category_menu {
    margin-top: 30px;
    height: 180px;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-pack: justify;
        justify-content: space-between;
}

.category_menu li a {
    color: #333333;
    text-decoration: none;
}

.category_menu li a:visited {
    color: #333333;
}

.category_menu li a:hover {
    color: #f2b22a;;
}

.sub_category h1 {
    font-family: "Open Sans", serif;
    font-weight: 130%;
}

.sub_category h2 {
    font-weight: 400;
}

.subtitle {
    position: relative;
}

.links li a {
    text-transform: uppercase;
    text-decoration: none;
    font-size: 14px;
    font-weight: bold;
}

.links li a:visited {
    color: #333333;
}

.selected {
    color: red;
    font-size: 20px;
}

.links li a:hover {
    color: #f2b22a;;
}

.underline {
    position: absolute;
    background: rgba(242, 178, 42, 0.7);
    width: 20px;
    height: 100%;
    z-index: -10;
    bottom: -2px;
}

nav {
    padding: 10px;
    border-radius: 10px;
}

.nav_content {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: center;
        align-items: center;
    max-width: 1200px;
    margin: 0 auto;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
}

.nav_content .logo {
    height: 40px;
}

ul {
    list-style: none;
    padding: 0;
}

.links {
    display: -ms-flexbox;
    display: flex;
    list-style: none;
    position: relative;
    width: 80%;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    max-width: 720px;
}

.topic {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    margin: 0 auto;
    position: relative;
    -ms-flex-align: center;
        align-items: center;
    -ms-flex-pack: center;
        justify-content: center;
    max-width: 860px;
    line-height: 160%;
}

.topic .submenu {
    position: absolute;
    left: -150px;
    top: 50vh;
    -webkit-transform: translateY(-50%);
        -ms-transform: translateY(-50%);
            transform: translateY(-50%);
    width: 180px;
}

.topic h2 {
    text-align: left;
    line-height: 35px;
}

.topic .submenu .content .bg {
    height: 75%;
    width: 180px;
    left: -20px;
    z-index: -5;
    top: 32px;
}

.topic .submenu li {
    margin-bottom: 15px;
}

.topic .submenu li a {
    font-size: 14px;
    color: rgba(51, 51, 51, 0.51);
    text-transform: uppercase;
    text-decoration: none;
}

input[type=text], select, textarea {
    width: 100%; /* Full width */
    padding: 12px; /* Some padding */
    border: 1px solid #ccc; /* Gray border */
    -webkit-box-sizing: border-box;
            box-sizing: border-box; /* Make sure that padding and width stays in place */
    margin-top: 6px; /* Add a top margin */
    margin-bottom: 16px; /* Bottom margin */
    resize: vertical /* Allow the user to vertically resize the textarea (not horizontally) */
}

input[type=submit] {
    background-color: #4CAF50;
    color: white;
    padding: 12px 20px;
    border: none;
    cursor: pointer;
}

.contact {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    padding-top: 50px;
}

.contact .form_container {
    background-color: #f2f2f2;
    padding: 20px;
    max-width: 500px;
}

.contact .right_info {
    margin-left: 30px;
    max-width: 600px;
    -ms-flex-item-align: start;
        align-self: flex-start;
}

@media screen and (max-width: 1200px) {
    .topic .submenu {
        left: 0;
        top: 0;
        position: relative;
        -webkit-transform: translateY(0);
            -ms-transform: translateY(0);
                transform: translateY(0);
        text-align: center;
    }

    .topic {
        display: -ms-flexbox;
        display: flex;
        -ms-flex-direction: column;
            flex-direction: column;
        margin: 0 auto;
        position: relative;
        -ms-flex-align: center;
            align-items: center;
        -ms-flex-pack: center;
            justify-content: center;
        max-width: 860px;
        line-height: 160%;
    }

    .se_container {
        -ms-flex-direction: column;
            flex-direction: column;
    }

    .se_container .right_menu {
        width: 90%;
        margin-left: 0;
    }

    .se_container .img_container {
        width: 100%;
    }
}

@media screen and (max-width: 830px) {
    .contact{
        -ms-flex-direction:column;
            flex-direction:column;
        -ms-flex-align: center;
            align-items: center;
    }
    nav {
        -ms-flex-direction: column;
            flex-direction: column;
    }

    nav .logo {
        margin-bottom: 10px;
    }

}

@media screen and (max-width: 690px) {
    .links {
        margin: 0 auto;
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: center;
            justify-content: center;
        -ms-flex-align: center;
            align-items: center;
    }
}


